.cart-preview {
    display: flex;
    height: 125px;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    border-bottom: 1px solid rgb(222, 218, 218);
    .img {
        width: 50px;
        height: 50px;
        background-color: rgb(161, 154, 154);
    }

    .item-title-price-container {
        display: flex;
        flex-direction: column;
        gap: 15px;
    }

    .item-container {
        display: flex;
        padding: 20px;
    }
    svg * {
        width: 30px;
        height: 30px;
    }
    img {
        margin-right: 20px;
        width: 50px;
        height: 50px;
    }

    p {
        margin: 0;
    }
}